
.lumos-header {
  background: #006dee;
  height: 2.4rem;
  color: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.lumos-header .button-left,
.lumos-header .button-right {
  -webkit-box-flex: 0.5;
  -ms-flex: 0.5;
  flex: 0.5;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
}
.lumos-header > .title {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  font-size: inherit;
  font-weight: 400;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
}
.lumos-header .button-left {
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  padding-left: 10px;
}
.lumos-header .button-right {
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  padding-right: 10px;
}
.lumos-header .button-right .button-icon {
  position: relative;
   width: 1.2rem;
  height: 1.2rem;
}
.lumos-header .button-right .button-icon img {
  width: 100%;
  width: 100%;
}
.lumos-header .button-right .button-icon .button-vonbadge {
  position: absolute;
  display: inline-block;
  padding: 2px 3px;
  z-index: 12;
  border-radius: 15px;
  background-color: #ea5a49;
  text-align: center;
  position: absolute;
  top: -6px;
  left: 50%;
  font-size: .5rem;
}
.lumos-header .button-right .button-text {
  margin-left: 0.3rem;
}



